@import url(https://fonts.googleapis.com/css?family=Roboto:400,700,900);
/* Resets */
html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 20px 0;
  padding: 0 20px;
  line-height: 1;
  font-family: "Roboto", sans-serif;
  color: #202020;
  background-color: #fbfbfb;
  font-smooth: always;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Pokemon App */
.pokemons_container {
  display: flex;
  max-width: 900px;
  margin: auto;
  flex-wrap: wrap;
  justify-content: space-between;
}

.pokemons_container p {
  margin: 0;
}

.pokemons_container .pokemon {
  width: 49%;
  background-color: white;
  background-clip: border-box;
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-radius: 0.25rem;
  box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075);
  overflow: hidden;
  margin-bottom: 20px;
}

.pokemons_container .pokemon_name {
  background-color: #ecd018;
  text-align: center;
  padding: 10px;
}

.pokemons_container .pokemon_name p {
  text-transform: uppercase;
  font-weight: bold;
  color: white;
  letter-spacing: 4px;
  text-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.4);
}

.pokemons_container .pokemon_image {
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 300px;
}

.pokemons_container .pokemon_image img {
  max-width: 100%;
  height: auto;
}

.pokemons_container .pokemon_attacks {
  display: flex;
  padding-left: 10px;
  padding-right: 10px;
  justify-content: space-between;
}

.pokemons_container .pokemon_attacks span {
  width: 30%;
  background-color: #f16820;
  border-radius: 3px;
  padding: 7px;
  font-weight: 700;
  color: #fff;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 12px;
  margin-bottom: 10px;
  word-wrap: break-down;
  text-align: center;
}

.pokemons_container .pokemon_meta {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
  padding: 0 10px;
}

.pokemons_container .pokemon_meta span {
  color: white;
  text-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.4);
  background-color: #7bb7b7;
  font-weight: bold;
  margin: 0;
  padding: 5px 20px;
  border-radius: 5px;
}

/* media queries */

@media (max-width: 800px) {
  .container .pokemon {
    width: 100%;
  }
}

@media (max-width: 400px) {
  .pokemons_container .pokemon__attacks,
  .pokemons_container .pokemon__meta {
    flex-wrap: wrap;
  }

  .pokemons_container .pokemon__meta span,
  .pokemons_container .pokemon__attacks span,
  .pokemons_container .pokemon {
    width: 100%;
  }

  .pokemons_container .pokemon__meta span {
    margin-bottom: 10px;
  }
}

